@import 'variables';
@import 'mixins';
@import 'reset';

body {
	font-size: 100%;
	color: #333333;
    font-family: $oswald;
    font-weight: 400;
}

@include selection {
    background-color: $blue;
    color: white;
}

header {
	position: relative;
	background-color: white;
	box-shadow: 0 3px 20px rgba(0,0,0,.1);
    .pre-header {
        display: none;
        padding: .45em 0;
        border-bottom: 1px solid #EEEEEE;
        @media (min-width: 992px) {
            display: block;
        }
        .pre-h-row {
            display: flex;
            justify-content: space-between;
            &__col {
                .pre-h-delivery {
                    display: flex;
                    align-items: center;
                    color: $blue;
                    span {
                        display: block;
                        font-size: .688em;
                        text-transform: uppercase;
                        a {
                            color: $blue;
                            text-decoration: underline;
                            transition: .3s;
                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
                .pre-h-nav {
                    display: flex;
                    li {
                        padding-left: 1.5em;
                        a {
                            display: flex;
                            align-items: center;
                            font-size: .688em;
                            color: $blue;
                            text-transform: uppercase;
                            transition: .3s;
                            span {
                                display: block;
                            }
                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
            }
        }
    }
    .header-top {
		padding: .75em 0;
		background-color: $blue;
		@media (min-width: 992px) {
			padding: .875em 0;
			background-color: white;
		}
        .head-top-row {
			display: flex;
			justify-content: space-between;
            &__col {
                &--logo {
					@media (min-width: 992px) {
						width: 20%;
					}
                    .logo-header {
						display: inline-block;
						width: 130px;
						@media (min-width: 992px) {
							width: auto;
						}
                        img {
							max-width: 100%;
						}
						&__lg {
							display: none;
							@media (min-width: 992px) {
								display: block;
							}
						}
						&__xs {
							display: block;
							@media (min-width: 992px) {
								display: none;
							}
						}
                    }
                }
                &--search {
					display: none;
					align-items: center;
					width: 50%;
					padding: 0;
					@media (min-width: 992px) {
						display: flex;
					}
					form {
						width: 100%;
					}
					.head-search {
						input {
							width: 100%;
							display: block;
							height: 40px;
							padding-left: 4.5em;
							background-color: $gray-bg;
							border: none;
							border-radius: 50px;
							font-size: .75em;
							text-transform: uppercase;
							transition: .3s;
							@include placeholder {
								color: #999999;
							}
							&:focus {
								background-color: white;
								box-shadow: 0 3px 15px rgba(58,82,163,.25);
							}
						}
					}
                }
                &--cart {
					display: flex;
					justify-content: flex-end;
					align-items: center;
					@media (min-width: 992px) {
						width: 30%;
					}
					.head-nav-cart {
						display: flex;
						align-items: center;
						&__item {
							&--login, &--register {
								display: flex;
								align-items: center;
								padding: 0 15px;
								a {
									display: flex;
									align-items: center;
									color: $blue;
									font-weight: 500;
									text-transform: uppercase;
									transition: .3s;
									span {
										display: none;
										font-size: .75em;
										@media (min-width: 992px) {
											display: block;
										}
									}
									&:hover {
										color: $red;
									}
								}
							}
							&--login {
								border-right: 1px solid #EEEEEE;
							}
							.btn-cart {
								display: flex;
								justify-content: center;
								align-items: center;
								color: white;
								transition: .3s;
								@media (min-width: 992px) {
									width: 60px;
									height: 30px;
									background-color: $red;
									border-radius: 50px;
								}
								span {
									display: block;
									font-size: .75em;
								}
								&:hover {
									box-shadow: 0 3px 10px rgba(255,70,75,.5);
								}
							}
							.btn-menu-xs {
								position: relative;
								width: 25px;
								height: 22px;
								margin-left: 1.25em;
								@media (min-width: 992px) {
									display: none;
								}
								span {
									position: absolute;
									width: 100%;
									height: 4px;
									display: block;
									background-color: white;
									border-radius: 50px;
									transition: .3s;
								}
								&__l1 {
									top: 0;
								} 
								&__l2 {
									@include center-y;
								}
								&__l3 {
									bottom: 0;
								}
								&.open {
									.btn-menu-xs {
										&__l1 {
											@include center-y;
											transform: rotate(45deg);
										}
										&__l2 {
											opacity: 0;
										}
										&__l3 {
											@include center-y;
											transform: rotate(-45deg);
										}
									}
								}
							}
						}
					}
				}
            }
        }
	}
	.header-nav {
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: white;
		left: -100%;
		opacity: 0;
		z-index: 20;
		transition: .3s;
		@media (min-width: 992px) {
			position: relative;
			width: auto;
			height: auto;
			left: 0;
			opacity: 1;
			z-index: auto;
		}
		&.open {
			left: 0;
			opacity: 1;
		}
		.head-menu-nav {
			display: flex;
			flex-direction: column;
			padding: 1em 0;
			@media (min-width: 992px) {
				flex-direction: row;
				justify-content: space-between;
				padding: 0;
			}
			&__item {
				width: 100%;
				@media (min-width: 992px) {
					width: auto;
				}
				.btn-nav-first {
					position: relative;
					display: block;
					padding: .75em 0;
					font-size: .75em;
					color: $gray-txt;
					font-weight: 500;
					text-transform: uppercase;
					transition: .3s;
					@media (min-width: 992px) {
						height: 100%;
						display: flex;
						align-items: center;
						justify-content: center;
						padding: .5em 0 1em;
						text-align: center;
						line-height: 1.3em;
					}
					span {
						display: block;
					}
					&:hover {
						color: $red;
					}
				}
			}
		}
	}
}